<template>
  <div>
    <ul>
      <li
        @click="toDetail(item.id)"
        v-for="item in dataList"
        :key="item.id"
        class="item-list clearfix"
      >
        <div class="list-left">
          <img :src="'https://images.weserv.nl/?url='+item.images.small" />
        </div>
        <div class="list-right">
          <h4>{{item.title}}</h4>
          <span>上映时间:{{item.mainland_pubdate}}</span>
          <br />影片类型:
          <span v-for="(item, index) in item.genres" :key="index">{{item}}/</span>
          <br />
          <span>时长:{{item.durations[0]}}</span>
        </div>
      </li>
    </ul>
    <div class="end" v-show="isEnd">
      <h3>数据到底了</h3>
    </div>
    <div class="loading" v-show="isLoading">
      <img src="../../assets/img/timg.gif" alt />
    </div>
  </div>
</template>
<script src="https://cdn.jsdelivr.net/npm/bootstrap@3.3.7/dist/js/bootstrap.min.js" integrity="sha384-Tc5IQib027qvyjSMfHjOMaLkfuWVxZxUPnCJA7l2mCWNIpG9mGCD8wGNIcPD7Txa" crossorigin="anonymous"></script>
<script>
import axios from "axios";
export default {
  data() {
    return {
      isLoading: false,
      dataList: [],
      nextStart: 0,
      isEnd: false
    };
  },
  created() {
    this.getData();
  },
  mounted() {
    window.onscroll = () => {
      let clientHeight = document.documentElement.clientHeight; //可视区域高度
      let scrollTop = document.documentElement.scrollTop; //当前滚动高度
      let scrollHeigth = document.documentElement.scrollHeight; //滚动条可滚动高度

      if (
        clientHeight + scrollTop >= scrollHeigth - 50 &&
        !this.isLoading &&
        this.dataList.length != 55
      ) {
        this.nextStart += 5;
        this.getData();
      }
      if (this.dataList.length == 55) {
        this.isEnd = true;
      }
    };
  },
  methods: {
    toDetail(id) {
      this.$router.push({
        name: "moviedetail",
        params: {
          id: id
        }
      }); //路由传参
      // this.$router.push(`/moviedetail/${movieId}`) //路由传参
    },
    getData() {
      let proxy = "https://bird.ioliu.cn/v2?url=";
      // let url ="https://douban.uieee.com/v2/movie/in_theaters?start=0&count=10";
      let url = `https://douban.uieee.com/v2/movie/in_theaters?start=${this.nextStart}&count=5`;
      this.isLoading = true;
      axios.get(proxy + url).then(res => {
        this.dataList = this.dataList.concat(res.data.subjects);
        this.isLoading = false;
      });
    }
  }
};
</script>
<style scoped>
.loading {
  position: fixed;
  top: 1rem;
  left: 0;
  right: 0;
  bottom: 1rem;
  background-color: #fff;
  opacity: 0.5;
}
.loading img {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  user-select: none;
}
ul {
  padding: 10px;
}
.item-list {
  border-bottom: 1px solid #ccc;
  padding: 10px 0;
}
.item-list .list-left img {
  width: 80%;
}
.list-left {
  float: left;
  width: 40%;
  text-align: center;
}
.list-right {
  float: left;
  width: 55%;
  padding-left: 5%;
}
.end {
  text-align: center;
}
</style>